.item-layout {
    margin-block: 15px;
    background-color: #7395AE;
    padding: 15px;
}

.item-layout img{
    width: 100%;
    margin-bottom: 10px;
}

.dot-list {
    display: flex;
    justify-content: center;
}

.dot-list .dot{
    width: 40px;
}

.dot-list .dot:hover{
    color: #5A5560;
}

.dot-list .dot_active{
    width: 40px;
    color: #5A5560;
}




