.task-list {
    list-style-type: circle;
}

 .task-list li {
    margin-bottom: 10px;

}

.intro {
    background-color: #7395AE;
    padding: 15px;
    margin-bottom: 10px;
}

.sider {
    background-color: #7395AE;
    padding: 15px;
    width: 35%;
    margin-right: 10px;

}

.content {
    display: flex;
}

.workexp {
    background-color: #7395AE;
    padding: 15px;
    margin-bottom: 15px;
}

.skill {
    display: grid;
    background-color: #7395AE;
    padding: 15px;
}

@media only screen and (max-width: 430px) {

    .sider {
        margin-bottom: 15px;
        width: auto;
        margin-right: 0;
    }

    .content {
        display: grid;
    }

}